<template>
    <div class="header">
        <router-link :to="{name:'root'}">
            <img src="../assets/cnodejs_light.svg">
        </router-link>
        <ul>
            <router-link :to="{name:'root'}">
                <li>首页</li>
                <li>新手入门</li>
                <li>API</li>
                <li>关于</li>
                <li>注册</li>
                <li>登录</li>
            </router-link>
        </ul>
    </div>
</template>

<script>
export default {
  name: 'Header'
}
</script>

<style scoped>

.header{
    background-color:#444444;
    height: 50px;
    margin:-8px;
    margin-bottom: 15px;
}
img{
    max-width: 120px;
    margin-left: 50px;
    margin-top: 10px;
}
ul{
    list-style: none;
    float: right;
    margin: 5px;
    padding-right: 18px;
}
li{
    display: inline-block;
    padding: 10px 10px;
}
a{
    text-decoration: none;
    color: #CCCCCC;
    font-size: 14px;
    text-shadow: none;
}
</style>



